IG$.mongoJobs = $s.extend(IG$.v$pnl, {
	scroll: false,
	closable: true,
	layout: "border",
	iconCls: "icon-mgr-db",

	_ld: function() {
		var me = this,
			mname = "MongoJobs",
			req = new IG$._rpc$();
		
		req.init(me, 
			{
				ack: "helpcontent",
				payload: {uid: "/" + mname, name: mname, type: "Folder", post: "create", duty: "everyone", mode: "s"},
				mbody: {option: "translate"}
			}, me, function(jdoc) {
				var me = this,
					tuid = jdoc && jdoc.length ? jdoc[0].uid : null;
					
				if (tuid)
				{
					me._tuid = tuid;
					me._ldc();
				}
			});
		req.send();
	},

	_ldc: function() {
		var me = this,
			tuid = me._tuid;

		if (tuid)
		{
			var panel = this,
				req = new IG$._rpc$();
				
			req.init(panel, 
				{
					ack: "content",
					payload: {uid: tuid},
					mbody: {option: ''}
				}, panel, function(jdoc) {
					var tnodes = (jdoc) ? jdoc.children : null,
						g1 = this.down("[name=g1]");
					g1.store.loadData(tnodes);
				}, false);
			req.send();
		}
	},

	_add: function() {
		var me = this,
			tuid = me._tuid;

		tuid && IG$._createMetaObject.call(me, "MongoMR", tuid, null, null, new IG$.callBackObj(me, function() {
			me._ldc();
		}));
	},

	_edit: function(rec) {
		var me = this,
			dlg = new IG$.mongoJobEdt({
				instance: me.instance,
				titem: rec,
				callback: new IG$.callBackObj(me, function() {
					me._ldc();
				})
			});
		
		IG$.checkLogin(me, dlg);
	},

	_exec: function() {
		var me = this,
			g1 = me.down("[name=g1]"),
			g1sel = g1.getSelectionModel().selected;

		if (g1sel && g1sel.items.length)
		{
			var mreq = me.getRequests(g1sel);
			// IG$.showScheduler(me, null, "MongoMR", mreq);

			var dlg = new IG$.d$RegSch({
				instance: me.instance,
				objtype: "mongomr",
				req: mreq,
				callback: new IG$.callBackObj(me, function(data) {
					// me.getSrvStat();
				})
			});
			IG$.checkLogin(me, dlg);
		}
	},

	getRequests: function(gsel) {
		var me = this,
			r = [],
			i, rec,
			cmd = "mapreduce",
			obj,
			cnt;

		for (i=0; i < gsel.items.length; i++)
		{
			rec = gsel.items[i];

			obj = {
				uid: rec.get("uid"),
				name: rec.get("name"),
				type: rec.get("type")
			};

			cnt = {option: "execute"};

			r.push({
				ack: cmd,
				payload: obj,
				mbody: cnt
			});
		}

		return r;
	},
	
	initComponent: function(){
		var me = this;
		
		me.title = IRm$.r1('L_MONGO_DPROC');
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					region: "center",
					flex: 1,
					title: IRm$.r1("L_MONGO_DPROC"), // "Features",
					layout: "fit",
					tbar: [
						{
						 iconCls: "icon-toolbar-add",
						 text: IRm$.r1("B_ADD"),
						 handler: function() {
							 this._add(); 
						 },
						 scope: this
						},
						{
							iconCls: "icon-toolbar-refresh",
							text: IRm$.r1("L_REFRESH"),
							handler: function() {
								this._ldc();
							},
							scope: this
						},
						"-",
						{
							text: IRm$.r1("L_RUN"),
							handler: function() {
								this._exec();
							},
							scope: this
						}
				 	],
					items: [
						{
							xtype: "gridpanel",
							name: "g1",
							store: {
								xtype: "store",
								fields: []
							},
							selType: "checkboxmodel",
							selModel: {
								mode: "MULTI",
								checkSelector: ".x-grid-cell"
							},
							columns: [
								{
									text: IRm$.r1("B_NAME"),
									flex: 1,
									tdCls: "igc-td-link",
									sortable: false,
									hideable: false,
									dataIndex: "name",
									xtype: "templatecolumn",
									renderer: function(value, metadata, record) {
										var tt = record.get("locale_name") || record.get("name");
										if (record.get("locale_name"))
										{
											tt += "(" + record.get("locale_name") + ")";
										}
										tt += "\n==>" + (record.get("nodepath") || "");
										// class='ig-navi-itemicon " + (record.get("iconcls") || "") + "' 
										return "<span title='" + IG$.hEE(tt) + "'>" + IG$.hEE(record.get("locale_name") || record.get("name")) + "</span>" + (record.data.attributes && record.data.attributes.attrb1 == "T" ? "  <span class='db_writable'></span>" : "");
									}
								},
								{
									text: IRm$.r1("B_OWNER"),
									width: 120,
									dataIndex: "owner"
								},
								{
									text: IRm$.r1("B_MODI"),
									width: 120,
									dataIndex: "modifier"
								},
								{
									text: IRm$.r1("B_LASTUPD"),
									width: 150,
									dataIndex: "updatedatefm"
								},
								{
									xtype: "actioncolumn",
									width: 60,
									items: [
										{
											iconCls: "icon-grid-dbicon",
											handler: function (grid, rowIndex, colIndex) {
												var rec = grid.store.data.items[rowIndex];
												this._edit(rec);
											},
											scope: this
										},
										{
											iconCls: "igc-share",
											tooltip: IRm$.r1("L_OBJECT_AUTH"),
											hidden: ig$.features["ig_n_oauth"] !== false, // || ig$.$l_ > 5,
											handler: function(grid, rowIndex, colIndex) {
												var rec = grid.store.data.items[rowIndex];
												this.openDSLayer("cmd_object_auth", rec);
											},
											scope: this
										},
										{
											iconCls: "icon-grid-delete",
											tooltip: IRm$.r1("L_REMOVE"),
											handler: function(grid, rowIndex, colIndex) {
												var me = this,
													store = grid.store,
													rec = store.getAt(rowIndex);

												IG$.deleteMetaObject(rec.get("uid"), rec, new IG$.callBackObj(me, function() {
													store.removeAt(rowIndex);
												}));
											},
											scope: this
										}
									]
								}
							],
							listeners: {
								cellclick: function(view, node, c, rec, rindex, e) {
									c == 1 && this._edit(rec);
								},
								scope: this
							}
						}
					]
				}
			]
		});
		
		IG$.mongoJobs.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function() {
			this._ld();
		}
	}
});