IG$.mongoJobAct = $s.extend($s.window, {

	width: 650,
	height: 580,
	layout: "fit",
	
	_init: function() {
        var me = this,
            ditem = me.ditem || {};

		me.setAllValues(ditem);
	},
	
	_confirm: function(cmd) {
        var me = this,
            ditem = me.ditem || {};

        if (me.getAllValues(ditem))
            return;

        me.callback && me.callback.execute(ditem);
        me.close();
	},
	
	initComponent: function(){
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("T_DB_CON"),
			padding: ig$.padding,
			items: [
				{
                    xtype: "panel",
                    layout: {
                        type: "vbox",
                        align: "stretch"
                    },
                    padding: ig$.padding,
                    autoScroll: true,
                    items: [
                        {
                            xtype: "fieldset",
                            title: IRm$.r1("L_MR_TGT"), // "Post Execution Statement",
                            layout: {
                                type: "vbox",
                                align: "stretch"
                            },
                            items: [
                                {
                                    xtype: "textfield",
                                    fieldLabel: IRm$.r1("B_NAME"),
                                    required: true,
                                    name: "name"
                                },
                                {
                                    xtype: "combobox",
                                    fieldLabel: IRm$.r1("B_ACTION"),
                                    required: true,
                                    valueField: "value",
                                    displayField: 'name',
									editable: false,
									autoSelect: true,
                                    name: "action_type",
                                    store: {
                                        data: [
                                            {name: "increment", value: "increment"},
                                            {name: "set", value: "set"},
                                            {name: "numeric add", value: "add"},
                                            {name: "array push", value: "push"},
                                            {name: "delete", value: "delete"}
                                        ]
                                    }
                                },
                                {
                                    xtype: "textarea",
                                    fieldLabel: "Catalog",
                                    name: "catalog"
                                }
                            ]
                        }
                    ]
                }
			],
			
			buttons: [
				{
					xtype: "button",
					text: IRm$.r1("B_DELETE"),
					hidden: (me.dbinfo ? false : true),
					handler: function() {
						this._confirm("delete");
					},
					scope: this
				},
				"->",
				{
					xtype: "button",
					text: IRm$.r1("B_CONFIRM"),
                    bodyCls: "ig-btn-confirm",
					handler: function() {
						this._confirm("confirm");
					},
					scope: this
				},
				{
					xtype: "button",
					text: IRm$.r1("B_CANCEL"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$.mongoJobAct.superclass.initComponent.call(this);
	},
	
	listeners: {
		afterrender: function(tobj) {
			tobj._init();
		}
	}
});



IG$.mongoJobEdt = $s.extend($s.window, {

	width: 650,
	height: 580,
	layout: "fit",
	
	_init: function() {
        var me = this,
            titem = me.titem,
            req = new IG$._rpc$();

        if (titem)
        {
			req.init(me, 
				{
					ack: "content",
					payload: {uid: titem.get("uid")},
					mbody: {option: ''}
				}, me, function(jdoc) {
                    me.titem_d = jdoc;
                    var cnt = jdoc.content = jdoc.content || {};
					me.setAllValues(cnt);
                    if (cnt.sql_ds)
                    {
                        me.down("[name=ds_name]").setValue(cnt.sql_ds.name);
                        me.sql_ds = cnt.sql_ds;
                    }
                    if (cnt.sql_dt)
                    {
                        me.down("[name=dt_name]").setValue(cnt.sql_dt.name);
                        me.sql_dt = cnt.sql_dt;
                    }
                    var g1 = me.down("[name=g1]");
                    g1.store.loadData(cnt.mr_actions);
				}, false);
			req.send();
        }
	},
	
	_confirm: function(cmd) {
        var me = this,
            titem = me.titem,
            itemdata = {},
            g1 = me.down("[name=g1]"),
            item = me.titem_d,
            mr_actions,
            i, rec;

        if (me.getAllValues(itemdata))
            return;

        item.name = itemdata.name;
        item.description = itemdata.description;

        itemdata.meta_type = "MongoMR";
        itemdata.mr_actions = mr_actions = [];

        if (me.sql_ds)
        {
            itemdata.sql_ds = me.sql_ds;
        }

        if (me.sql_dt)
        {
            itemdata.sql_dt = me.sql_dt;
        }

        for (i=0; i < g1.store.data.items.length ;i++)
        {
            rec = g1.store.data.items[i];
            mr_actions.push({
                name: rec.get("name"),
                action_type: rec.get("action_type"),
                catalog: rec.get("catalog")
            });
        }
        
        var req = new IG$._rpc$();
        req.init(me, 
            {
                ack: "writecontent",
                payload: {
                    name: item.name,
                    description: item.description,
                    uid: item.uid
                },
                mbody: itemdata,
                meta_type: "MongoMR"
            }, me, function(jdoc) {
                me.callback && me.callback.execute();
                me.close();
            });
        req.send();
	},

    _act: function(rec) {
        var tdata = rec ? rec.data : null;

        var me = this,
            g1 = me.down("[name=g1]"),
			dlg = new IG$.mongoJobAct({
				instance: me.instance,
				ditem: tdata,
				callback: new IG$.callBackObj(me, function(mdata) {
					if (!rec)
                    {
                        g1.store.add(mdata);
                    }
				})
			});
		
		IG$.checkLogin(me, dlg);
    },

    _ldc: function() {
        var me = this,
            g1 = me.down("[name=g1]"),
            g1store = g1.store,
            sel = g1.getSelectionModel().selected,
            i;

        if (sel && sel.length)
        {
            IG$.confirmMessages(ig$.appname, IRm$.r1("M_CONFIRM_DELETE"), function(e) {
                if (e == "yes")
                {
                    g1store.remove(sel.items[0]);
                }
            });
        }
    },

    _ldcm: function(direction) {
        var me = this,
            g1 = me.down("[name=g1]");

        IG$._moveG.call(me, g1, direction);
    },
	
	initComponent: function(){
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("T_DB_CON"),
			padding: ig$.padding,
			items: [
				{
                    xtype: "panel",
                    layout: {
                        type: "vbox",
                        align: "stretch"
                    },
                    padding: ig$.padding,
                    autoScroll: true,
                    items: [
                        {
                            xtype: "fieldset",
                            title: IRm$.r1("B_GENERAL"), // "Post Execution Statement",
                            layout: {
                                type: "vbox",
                                align: "stretch"
                            },
                            items: [
                                {
                                    xtype: "textfield",
                                    required: true,
                                    fieldLabel: IRm$.r1("B_NAME"),
                                    name: "name"
                                },
                                {
                                    xtype: "textarea",
                                    fieldLabel: IRm$.r1("B_DESC"),
                                    name: "description"
                                }
                            ]
                        },
                        {
                            xtype: "fieldset",
                            title: IRm$.r1("L_MR_SRC"), // "Post Execution Statement",
                            layout: {
                                type: "vbox",
                                align: "stretch"
                            },
                            items: [
                                {
                                    xtype: "combobox",  
                                    fieldLabel: IRm$.r1("B_ACTION"),
                                    required: true,
                                    valueField: "value",
                                    displayField: 'name',
									editable: false,
									autoSelect: true,
                                    name: "source_type",
                                    store: {
                                        data: [
                                            {name: IRm$.r1("B_SELECT"), value: ""},
                                            {name: "sql", value: "sql"},
                                            {name: "file", value: "file"}
                                        ]
                                    },
                                    listeners: {
                                        change: function(tobj, nvalue, ovalue) {
                                            var me = this;

                                            me.down("[name=sql_query]").required = nvalue == "sql";

                                            $.each(["sql", "file"], function(i, m) {
                                                var v = me.down("[name=src_" + m + "]");
                                                v.setVisible(m == nvalue);
                                            });
                                        },
                                        scope: this
                                    }
                                },
                                {
                                    xtype: "container",
                                    hidden: true,
                                    name: "src_sql",
                                    layout: {
                                        type: "vbox",
                                        align: "stretch"
                                    },
                                    items: [
                                        {
                                            xtype: "fieldcontainer",
                                            fieldLabel: IRm$.r1("L_DATASOURCE"),
                                            layout: {
                                                type: "hbox",
                                                align: "stretch"
                                            },
                                            items: [
                                                {
                                                    xtype: "textfield",
                                                    name: "ds_name",
                                                    readOnly: true,
                                                    flex: 1
                                                },
                                                {
                                                    xtype: "button",
                                                    text: "..",
                                                    handler: function() {
                                                        var me = this,
                                                                dlg;
                                                            
                                                            dlg = new IG$.dlg_select_ds({
                                                                instance: me.instance,
                                                                sel_mode: 1,
                                                                callback: new IG$.callBackObj(me, function(sel) {
                                                                    var me = this;
                                                                    me.sql_ds = sel.data;
                                                                    me.down("[name=ds_name]").setValue(me.sql_ds.name);
                                                                })
                                                            });
                                                            
                                                            IG$.checkLogin(me, dlg);
                                                    },
                                                    scope: this
                                                }
                                            ]
                                        },
                                        {
                                            xtype: "textarea",
                                            fieldLabel: IRm$.r1("B_SQL"),
                                            required: true,
                                            name: "sql_query"
                                        }
                                    ]
                                },
                                {
                                    xtype: "container",
                                    hidden: true,
                                    name: "src_file",
                                    layout: {
                                        type: "vbox",
                                        align: "stretch"
                                    },
                                    items: [
                                        {
                                            xtype: "textfield",
                                            fieldLabel: IRm$.r1("L_TARGET_FOLDER"),
                                            name: "src_work_folder"
                                        },
                                        {
                                            xtype: "textfield",
                                            fieldLabel: IRm$.r1("L_FILENAME"),
                                            name: "src_filenames"
                                        }
                                    ]
                                }
                            ]
                        },
                        {
                            xtype: "fieldset",
                            title: IRm$.r1("L_MR_TGT"), // "Post Execution Statement",
                            layout: {
                                type: "vbox",
                                align: "stretch"
                            },
                            
                            items: [
                                {
                                    xtype: "combobox",  
                                    fieldLabel: IRm$.r1("B_ACTION"),
                                    required: true,
                                    valueField: "value",
                                    displayField: 'name',
									editable: false,
									autoSelect: true,
                                    name: "target_type",
                                    store: {
                                        data: [
                                            {name: IRm$.r1("B_SELECT"), value: ""},
                                            {name: "mongodb", value: "mongodb"},
                                            {name: "file", value: "file"},
                                            {name: "flink", value: "flink"}
                                        ]
                                    },
                                    listeners: {
                                        change: function(tobj, nvalue, ovalue) {
                                            var me = this;
                                            me.down("[name=c_dtarget]").setVisible(nvalue == "mongodb");
                                            me.down("[name=t_file]").setVisible(nvalue == "file");
                                            me.down("[name=dt_flink]").setVisible(nvalue == "flink");
                                            me.down("[name=g1]").setVisible(nvalue != "flink");
                                        },
                                        scope: this
                                    }
                                },
                                {
                                    xtype: "fieldcontainer",
                                    hidden: true,
                                    name: "t_file",
                                    fieldLabel: IRm$.r1("L_FILE"),
                                    layout: {
                                        type: "hbox",
                                        align: "stretch"
                                    },
                                    items: [
                                        {
                                            xtype: "textfield",
                                            name: "dt_file",
                                            flex: 1
                                        }
                                    ]
                                },
                                {
                                    xtype: "fieldcontainer",
                                    name: "c_dtarget",
                                    hidden: true,
                                    fieldLabel: IRm$.r1("L_DATASOURCE"),
                                    layout: {
                                        type: "hbox",
                                        align: "stretch"
                                    },
                                    items: [
                                        {
                                            xtype: "textfield",
                                            name: "dt_name",
                                            readOnly: true,
                                            flex: 1
                                        },
                                        {
                                            xtype: "button",
                                            text: "..",
                                            handler: function() {
                                                var me = this,
                                                        dlg;
                                                    
                                                    dlg = new IG$.dlg_select_ds({
                                                        instance: me.instance,
                                                        sel_mode: 1,
                                                        dbtype: "mongodb",
                                                        callback: new IG$.callBackObj(me, function(sel) {
                                                            var me = this;
                                                            me.sql_dt = sel.data;
                                                            me.down("[name=dt_name]").setValue(me.sql_dt.name);
                                                        })
                                                    });
                                                    
                                                    IG$.checkLogin(me, dlg);
                                            },
                                            scope: this
                                        }
                                    ]
                                },
                                {
                                    xtype: "fieldcontainer",
                                    hidden: true,
                                    name: "t_flink",
                                    fieldLabel: IRm$.r1("L_FILE"),
                                    layout: {
                                        type: "hbox",
                                        align: "stretch"
                                    },
                                    items: [
                                        {
                                            xtype: "textfield",
                                            name: "dt_flink",
                                            flex: 1
                                        }
                                    ]
                                },
                                {
                                    xtype: "gridpanel",
                                    name: "g1",
                                    height: 300,
                                    selType: "checkboxmodel",
									selModel: {
										mode: "MULTI",
										checkSelector: ".x-grid-cell"
									},
                                    store: {
                                        xtype: "store",
                                        fields: []
                                    },
                                    columns: [
                                        {
                                            text: IRm$.r1("B_NAME"),
                                            dataIndex: "name"
                                        }
                                    ],
                                    listeners: {
                                        cellclick: function(view, node, c, rec, rindex, e) {
                                            c == 1 && this._act(rec);
                                        },
                                        scope: this
                                    },
                                    tbar: [
                                        {
                                         iconCls: "icon-toolbar-add",
                                         text: IRm$.r1("B_ADD"),
                                         handler: function() {
                                             this._act(); 
                                         },
                                         scope: this
                                        },
                                        {
                                            iconCls: "icon-toolbar-remove",
                                            text: IRm$.r1("B_DELETE"),
                                            handler: function() {
                                                this._ldc();
                                            },
                                            scope: this
                                        },
                                        "-",
                                        {
                                            iconCls: "icon-toolbar-up",
                                            text: IRm$.r1("B_UP"),
                                            handler: function() {
                                                this._ldcm(-1);
                                            },
                                            scope: this
                                        },
                                        {
                                            iconCls: "icon-toolbar-down",
                                            text: IRm$.r1("B_DOWN"),
                                            handler: function() {
                                                this._ldcm(1);
                                            },
                                            scope: this
                                        }
                                    ]
                                }
                            ]
                        }
                    ]
                }
			],
			
			buttons: [
				{
					xtype: "button",
					text: IRm$.r1("B_DELETE"),
					hidden: (me.dbinfo ? false : true),
					handler: function() {
						this._confirm("delete");
					},
					scope: this
				},
				"->",
				{
					xtype: "button",
					text: IRm$.r1("B_CONFIRM"),
                    bodyCls: "ig-btn-confirm",
					handler: function() {
						this._confirm("confirm");
					},
					scope: this
				},
				{
					xtype: "button",
					text: IRm$.r1("B_CANCEL"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$.mongoJobEdt.superclass.initComponent.call(this);
	},
	
	listeners: {
		afterrender: function(tobj) {
			tobj._init();
		}
	}
});

